import React, {Fragment, useState , useCallback} from 'react'
import {contacts} from '../contacts'
import Person from './Person';



const PersonList = () => {

    const [list, setList ] = useState(contacts)

    const handleRemove = (id) => {
        const tempList = list.filter(item => item.id !== id);
        setList(tempList)
    }
    const handleChange = (id) => { 
        const tempList = list.map(item => { 
            if (item.id === id) { 
                const updatedItem = {
                    ...item,
                    name: item.name,
                    isComplete: !item.isComplete
                };
                return updatedItem
            }
            return item
        });
        setList(tempList)
    }


    return (
        <Fragment>
            <div className="row">
                <Person list={list} onRemove={handleRemove} handleChange={handleChange}   />
                <div className="card">
                    <div className="buttonsWrapper ">
                    <button className="btn plus-icon " type="button"><i className="fas fa-plus-square" /> Add new person</button>

                </div> 
                </div>

            </div>
        </Fragment>
     
  
    )
};

export default PersonList
