import React, { Fragment, useState, useEffect } from 'react'
import { Modal, Button } from "react-bootstrap";
import axios from 'axios';



const Person = ({ list, onRemove, handleChange }) => {

    const [showModal, setShow] = useState(false);
    const handleShow = () => setShow(true);
    const handleClose = () => setShow(false);
    const [phone, setPhone] = useState('');


    const onChange = e => ({  [e.target.name]: e.target.value });

    const [lat, setLat] = useState('');
    const [long, setLong] = useState('');

    // useEffect(() => {
    //     axios.get(`https://maps.googleapis.com/maps/api/geocode/json?address=${list.location}&key=AIzaSyBoAjmIZ_tE0NXWp0J1fpNOVy5KWh8WJi0`)
    //         .then(res => {
    //             setLat(res.data.results[0].geometry.location.lat);
    //             setLong(res.data.results[0].geometry.location.lng);
    //         })
    // }, []);

    const checkPhone = () => {
        const phoneRegex = /^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/;
        return phoneRegex.test(setPhone);
    };

    return (
        <Fragment>
            {list.map(item => (
                <div className="card">
                    <div className="card-body">
                        <img className="card-img-top" src={`../images/${item.img}`} alt="" />
                        <h4 className="card-title"> {item.name} </h4>
                        <p className="card-text">{item.description}</p>
                        <p className="card-text">{item.address}</p>
                        <p className="card-text">{item.city} </p>
                    </div>
                    <div className="buttonsWrapper">
                        <button className="btn deleteBtn" onClick={() => onRemove(item.id)} type="button"><i className="fas fa-trash-alt" /></button>
                        <button data-toggle="modal" data-target="#modalEdit" className="btn editBtn" type="button" onClick={handleShow}><i className="fas fa-edit" /></button>
                    </div>
                    <Modal show={showModal} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>Edit User</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                <form className="form" >
                        <label for="name"> Name </label>
                        <input type="text" placeholder=" * name" name="name" value={item.name} onChange={(e) => onChange(e)}   />
                        <label for="description"> description </label>
                        <input type="text" placeholder=" * description" name="item.description" value={item.description} onChange={(e) => onChange(e)}   />
                        <label for="company"> company </label>
                        <input type="text" placeholder=" * company" name="item.company" value={item.company} onChange={(e) => onChange(e)}   />
                        <label for="address"> location </label>
                        <input type="text" placeholder=" * location" name="item.location" value={item.address} onChange={(e) => onChange(e)}   />                        
                        <label for="address"> phone </label>
                        <input type="text" placeholder=" * phone" name="item.phone" value={item.phone} onChange={(e) => onChange(e)}   />                        
                    </form>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={() => handleChange(item.id)} >
                    {item.isComplete ? 'Undo' : 'Done'}  
      
                    </Button>
                </Modal.Footer>
            </Modal>
                </div>
                
            ))}

         

        </Fragment>
    )
}

export default Person
